.cards_container{
    width: 53vw;
    margin: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 35px;
    z-index: 1;
    border: 5px dotted rgba(156, 176, 185, 0.397);

    @media (max-width: 992px) {
        width: 75vw;
        
    }


    .card{
        height: 100px;
        width: 120px;
        margin: 10px;
        
        position: relative;
        border-radius: 15px;
        font-size: 1.5rem;
        color: white;
        cursor: pointer;
        
        
        

        

        .delete_btn{
            position: absolute;
            top: 0px;
            left: 95px;
            color: rgba(90, 64, 61, 0.295);
    
        }

        .card_side{
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 15px;
            font-size: 1.5rem;
        }

        .card_side_translate{
            background-color: rgb(205, 183, 159);
            color: white;
        }

        .card_side_word{
            background-color: rgb(255, 255, 255);
            color:  rgb(205, 183, 159);
        }
    }
}

.no_cards_msg{
    font-size: 2rem;
    color: rgb(118, 141, 137);
    margin: 40px;
    padding: 15px;
    text-align: center;
    z-index: 3;
    background-color: rgba(241, 245, 244, 0.822);
    border-radius: 35px;
}